export default {
    data(){
        return{}
    },
    watch:{
        LoopPieData(curVal,oldVal){
            this.drawLoopPie()
        }
    },
    mounted(){
        this.drawLoopPie()
    },
    methods:{
        drawLoopPie(){
            let that = this
            let LoopPie = this.$echarts.init(this.$refs.echarts)
            LoopPie.setOption({
                // tooltip: {},
                color:[
                    'rgba(254, 205, 93, 1)',
                    'rgba(64, 137, 255, 1)',
                    'rgba(37, 208, 150, 1)',
                    'rgba(170, 137, 189, 1)',
                    'rgba(255, 85, 153, 1)',
                ],
                legend: {
                    itemWidth: 10,
                    itemHeight: 10,
                    itemGap: 10,
                    x:'center',
                    y:'bottom',
                },
                series : [
                    {
                        name: 'Looppie',
                        type: 'pie',
                        radius: ['30%', '60%'],
                        center: ['50%', '40%'],
                        data:[
                            {value:335, name:'111'},
                            {value:310, name:'222'},
                            {value:234, name:'333'},
                            {value:135, name:'444'},
                            {value:1548, name:'555'}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            })
            window.addEventListener("resize", LoopPie.resize) // echarts提供自动缩放
        }
    }
}



